import {
  Component,
  ContentChild,
  TemplateRef,
  Inject,
  forwardRef
} from "@angular/core";

import { connectStats } from "instantsearch.js/es/connectors";

import { BaseWidget } from "../base-widget";
import { NgAisInstantSearch } from "../instantsearch/instantsearch";


export class NgAisStats extends BaseWidget {
   public template: any;

  public state = {
    hitPerPage: 0,
    nbHits: 0,
    nbPages: 0,
    page: 0,
    processingTimeMS: 0,
    query: ""
  };

  get templateContext() {
    return { state: this.state };
  }

  constructor(
    
    public instantSearchParent: any
  ) {
    super("Stats");
    this.createWidget(connectStats);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: "ng-ais-stats",
  template: `
    <div [class]="cx()">
      <ng-container *ngTemplateOutlet="template; context: templateContext">
      </ng-container>

      <span *ngIf="!template" [class]="cx('text')">
        {{state.nbHits}} results found in {{state.processingTimeMS}}ms.
      </span>
    </div>
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: undefined, decorators: [{ type: Inject, args: [forwardRef(() => NgAisInstantSearch), ] }, ]},
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'template': [{ type: ContentChild, args: [TemplateRef, ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
